@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:500,700&display=swap);
body {
  margin: 0;
  padding: 0; }

.banner-overlay {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.banner button {
  font-family: "Source Code Pro", monospace;
  font-size: 28px;
  font-weight: 700;
  border-radius: 0;
  padding: 6px 12px;
  color: white;
  border: solid 3px white;
  background-color: transparent;
  box-shadow: 0 0 8px #181a21;
  text-shadow: 0 0 8px #181a21;
  -webkit-transition: border-color 0.2s, background-color 0.2s, text-shadow 0.2s;
  transition: border-color 0.2s, background-color 0.2s, text-shadow 0.2s; }

.banner button:hover {
  border-color: #9961c2;
  background-color: #9961c2;
  text-shadow: none; }

.banner h1 {
  color: white;
  font-family: "Source Code Pro", monospace;
  font-size: 48px;
  text-align: center;
  text-shadow: 0 0 8px #181a21;
  margin: 0;
  margin-bottom: 32px; }

span {
  background-color: #9961c2;
  color: white;
  text-shadow: none;
  box-shadow: 0 0 8px #181a21;
  font-weight: 700; }


