$font-stack: 'Source Code Pro', monospace;
$shadow-style: 0 0 8px #181a21;
$primary-color: #9961c2;
$button-transition-time: 0.2s;

body {
    margin: 0;
    padding: 0;
}

.banner-overlay {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 40vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.banner button {
    font-family: $font-stack;
    font-size: 28px;
    font-weight: 700;
    border-radius: 0;
    padding: 6px 12px;
    color: white;
    border: solid 3px white;
    background-color: transparent;
    box-shadow: $shadow-style;
    text-shadow: $shadow-style;
    transition: border-color $button-transition-time, 
                background-color $button-transition-time,
                text-shadow $button-transition-time;
}

.banner button:hover {
    border-color: $primary-color;
    background-color: $primary-color;
    text-shadow: none;
}

.banner h1 {
    color: white;
    font-family: $font-stack;
    font-size: 48px;
    text-align: center;
    text-shadow: $shadow-style;
    margin: 0;
    margin-bottom: 32px;
}

span {
    background-color: $primary-color;
    color: white;
    text-shadow: none;
    box-shadow: $shadow-style;
    font-weight: 700;
}